.render-page {
  padding: 8px 10px;
  border: 1px dashed #000;
  border-radius: 5px;
}
.render-page .page-item {
  padding: 10px 5px;
  position: relative;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin: 10px 0;
  background: #eee;
}
.render-page .page-item .item-close {
  position: absolute;
  right: -8px;
  top: -10px;
}
.render-page .page-item .item-close .item-close-icon {
  font-size: 20px;
  background: #fff;
  border-radius: 100%;
}
.render-page .component-selected {
  border-color: red;
}