.thanos-generate-page-container .page-wrapper {
  display: flex;
  height: calc(100vh - 56px);
}
.thanos-generate-page-container .page-wrapper .left-toolbar {
  width: 50px;
  height: 100%;
  padding: 12px 10px;
  text-align: center;
}
.thanos-generate-page-container .page-wrapper .left-toolbar .anticon {
  display: block;
  padding: 6px;
  cursor: pointer;
}
.thanos-generate-page-container .page-wrapper .left-toolbar .scale-percent {
  font-size: 12px;
}
.thanos-generate-page-container .page-wrapper .page-shower {
  display: flex;
  flex: 1;
  height: 100%;
  background: #dcdcdc;
  justify-content: center;
  align-items: center;
}
.thanos-generate-page-container .page-wrapper .page-shower .canvas {
  width: 100%;
  height: 100%;
  border: 1px solid #dcdcdc;
  box-sizing: border-box;
  background: #fff;
}
.thanos-generate-page-container .page-wrapper .page-shower .canvas .thanos-page {
  display: flex;
  flex-direction: "column";
}
.thanos-generate-page-container .page-wrapper .page-shower .canvas .thanos-page .thanos-page-container {
  flex: 1;
  padding: 15px;
}
.thanos-generate-page-container .page-wrapper .page-shower .canvas .thanos-page .thanos-page-operation {
  width: 256px;
  background-color: #fafafa;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 56px);
}
.thanos-generate-page-container .page-wrapper .page-shower .canvas .thanos-page .thanos-page-operation .thanos-page-operation-bar-tools {
  width: 44px;
  padding: 5px;
  text-align: center;
  border-left: 1px solid #ccd5db;
  border-right: 1px solid #ccd5db;
  background-color: white;
}