.thanos-common {
    .header {
        width: 100%;
        height: 56px;
        background: #fafafa;
        display: flex;
        align-items: center;
    }
    
    .title {
        margin-left: 12px;
        font-size: 28px;
        font-weight: bold;
        font-family: "STHeiti"
    }
    
    .sub-title {
        font-size: 16px;
    }
    
    .user {
        flex: 1;
        width: 230px;
        text-align: right;
        padding-right: 10px;
    
        .welcome {
            width: 30px;
            height: 30px;
        }
    }
}
